/*------------------------------------*
 *  # SASS Mixins and Functions
 *------------------------------------*/

/*
 * SASS interpolation
 */
// Requires the calc-interpolation function which can also be used independently
@function calc-interpolation($min-screen, $min-value, $max-screen, $max-value) {
    $a: ($max-value - $min-value) / ($max-screen - $min-screen);
    $b: $min-value - $a * $min-screen;

    $sign: '+';
    @if ($b < 0) {
        $sign: '-';
        $b: abs($b);
    }
    @return calc(#{$a * 100}vw #{$sign} #{$b});
}

@mixin interpolate($properties, $min-screen, $max-screen, $min-value, $max-value) {
    & {
        @each $property in $properties {
            #{$property}: $min-value;
        }
        @media screen and (min-width: $min-screen) {
            @each $property in $properties {
                #{$property}: calc-interpolation($min-screen, $min-value, $max-screen, $max-value);
            }
        }
        @media screen and (min-width: $max-screen) {
            @each $property in $properties {
                #{$property}: $max-value;
            }
        }
    }
}

/*
 * PX to EM
 * @param $px - px value to be converted
 * @param $base - base font size (in `em`)
 * Note: 'em' values are calculate based on the element font-size
 *       to properly converts 'px' to 'em', please pass in the element font-size with it
 * Usage example:
    padding: em(16px, 1.6em); // font-size in 'em'
 */
@function em($px, $base: $BASE_FONT_SIZE) {
    $list: ();
    @if length($px) != 1 {
        @for $i from 1 through length($px) {
            $val_em: (nth($px, $i) / $base) * 1em;
            $list: append($list, $val_em, space);
        }
        @return $list;
    } @else {
        @return ($px / $base) * 1em;
    }
}

/*
 * Set property by passing a property name, and values.
 * @param $property name - padding, margin etc.
 * @param $values        - values in `px` (space separated for multiple values)
 * @param $font-size     - base font-size in `em`
 * Usage example:
   @include toEm(padding, 8px 16px 8px, 1.6em);
 */
@mixin toEm($property, $values, $font-size: $BASE_FONT_SIZE) {
    #{$property}: em($values, $font-size);
}

/*
 * Generate typefaces key-value pair of variable name and config
 * Example:
   --paragraph-center-bold-black: (14px, center, bold, black)
 */
@function generate-typefaces() {
    $typeface_list: ();
    @each $fontsize_name, $size in $FONT_SIZES {
        @each $textalign_name, $text_align in $TEXT_ALIGN {
            @each $color_name, $color in $FONT_COLORS {
                @each $fontweight_name, $weight in $FONT_WEIGHTS {
                    $key: --#{$fontsize_name}-#{$textalign_name}-#{$fontweight_name}-#{$color_name};
                    $val: ($size, $text_align, $weight, $color);
                    $typeface: (
                        $key: $val,
                    );
                    $typeface_list: map-merge($typeface_list, $typeface);
                }
            }
        }
    }
    // @debug $typeface_list; /* uncomment to debug */
    @return $typeface_list;
}

/*
 * Sets font-size, font-weight, color, text-transform, text-align and line-height
 * Usage example:
   @include setTypeface(16px, bold, black, uppercase);
 */
@mixin setTypeface($size, $align, $weight, $colour, $tt) {
    @if $size {
        font-size: $size;
    }
    @if $colour {
        color: $colour;
    }
    @if $weight {
        font-weight: $weight;
    }
    @if $tt {
        text-transform: $tt;
    }
    @if $align {
        text-align: $align;
    }
    @if $size == 1.6rem {
        line-height: map-get($map: $LINEHEIGHTS, $key: 'large');
    } @else if $size == 1.4rem {
        line-height: map-get($map: $LINEHEIGHTS, $key: 'medium');
    } @else if $size == 1.2rem {
        line-height: map-get($map: $LINEHEIGHTS, $key: 'small');
    } @else if $size == 1rem {
        line-height: map-get($map: $LINEHEIGHTS, $key: 'xsmall');
    } @else {
        line-height: unset;
    }
}

/*
 * Set typefaces by passing a typeface variable name
 * @param $var   - typeface variable
 * @param $tt    - text-transform property
 * @param $align - text-align property
 * Usage example:
   @include typeface(--paragraph-bold-black, uppercase, center);
 */
@mixin typeface($var, $tt: none) {
    $typeface_config: map-get(
        $map: $TYPEFACES_LIST,
        $key: $var,
    );

    $size: nth($typeface_config, 1); // font-size
    $align: nth($typeface_config, 2); // text-align
    $weight: nth($typeface_config, 3); // font-weight
    $color: nth($typeface_config, 4); // color
    @include setTypeface($size, $align, $weight, $color, $tt);
}

/*
 * Range Slider Thumb styling is set here
 */
@mixin thumbStyle() {
    appearance: none;
    width: 1em;
    height: 1em;
    border-radius: 100%;
    border: 0;
    cursor: pointer;
    transition: box-shadow 0.2s;
    box-shadow: 0 0 0 0px rgba(0, 0, 0, 0);

    &:hover {
        box-shadow: 0 0 0 0.5em var(--state-hover);
    }
}

/*
 * SVG icons colors
 */
@mixin colorIcon($colors...) {
    @for $i from 1 through length($colors) {
        /* postcss-bem-linter: ignore */
        .color#{$i}-fill {
            fill: nth($colors, $i);
        }
        /* postcss-bem-linter: ignore */
        .color#{$i}-stroke {
            stroke: nth($colors, $i);
        }
    }
}

/*
 * Bar Spinner Animation
 * @param $count    - number of bars
 * @param $duration - duration of animation
 * @param $phase    - each bar phase change delay
 * Usage example:
   @include createBarspinnerAnimation(5, 1.2s, 0,1);
 */
@mixin createBarspinnerAnimation($count, $duration, $phase) {
    animation: sk-stretchdelay $duration infinite ease-in-out;

    @for $i from 1 through $count {
        &--#{$i} {
            animation-delay: -$duration + (($i - 1) * $phase);
        }
    }
}

/*
 * Tooltip colors
 * @param $color - color property
 * Usage example:
   @include tooltipColor($COLOR_RED);
 */
@mixin tooltipColor($color) {
    &:before {
        background: $color;
    }
    &:after,
    &[data-tooltip-pos='top']:after {
        border-top-color: $color;
    }
    &[data-tooltip-pos='right']:after {
        border-top-color: transparent;
        border-right-color: $color;
    }
    &[data-tooltip-pos='left']:after {
        border-top-color: transparent;
        border-left-color: $color;
    }
    &[data-tooltip-pos='bottom']:after {
        border-top-color: transparent;
        border-bottom-color: $color;
    }
}

/*
 * Convert Tooltip colors
 * @param $color - css color property
 * Usage example:
   @include convertTooltipColor(var(--status-default));
 */
@mixin convertTooltipColor($color) {
    &:before {
        background: $color;
    }
    &:after,
    &[data-tooltip-pos='top']:after {
        border-top-color: $color;
    }
    &[data-tooltip-pos='right']:after {
        border-top-color: transparent;
        border-right-color: $color;
    }
    &[data-tooltip-pos='left']:after {
        border-top-color: transparent;
        border-left-color: $color;
    }
    &[data-tooltip-pos='bottom']:after {
        border-top-color: transparent;
        border-bottom-color: $color;
    }
}

/*
    Constants
*/
$FONT_STACK: 'IBM Plex Sans', sans-serif;
$BASE_FONT_SIZE: 10px;

/*
    Variables
*/
$FONT_SIZES: (
    'xheading' 3.2rem,
    'heading' 3rem,
    'large' 2.8rem,
    'title' 1.6rem,
    'paragraph' 1.4rem,
    'small' 1.2rem,
    'xsmall' 1rem,
    'xxsmall' 0.8rem,
    'xxxsmall' 0.6rem
);

$FONT_COLORS: (
    'active' var(--text-colored-background),
    'prominent' var(--text-prominent),
    'black' var(--text-general),
    'grey' var(--text-less-prominent),
    'disabled' var(--text-disabled),
    'green' var(--text-profit-success),
    'red' var(--text-loss-danger)
);

$FONT_WEIGHTS: ('bold' 700, 'semibold' 500, 'normal' 400, 'light' 300);

$TEXT_ALIGN: ('center' center, 'left' left, 'right' right);

$LINEHEIGHTS: (
    'large': 1.75,
    'medium': 1.5,
    'small': 1.25,
    'xsmall': 1.4,
);

/*
    List of all typefaces variables in the format: --$FONT_SIZES-$TEXT_ALIGN-$FONT_WEIGHTS-COLORS.
    See $FONT_SIZES, $TEXT_ALIGN, $FONT_WEIGHTS, $COLORS maps above for references.
    Variables name example: --title-center-semibold-red
 */
$TYPEFACES_LIST: generate-typefaces();

/*
    to use styles without modifier class on .inline-icon
    use @extend

    e.g. style icon on parent hover:
        a:hover .inline-icon {
            @extend %inline-icon.active;
        }
*/

%inline-icon {
    @include colorIcon(var(--text-general), none);

    &.active {
        @include colorIcon(var(--text-prominent), none);
    }
    &.disabled {
        @include colorIcon(var(--text-disabled), none);
    }
    &.white {
        @include colorIcon(var(--text-prominent));
    }
    &.border_hover_color {
        @include colorIcon(var(--text-prominent));
    }
    &.secondary {
        @include colorIcon(var(--text-less-prominent));
    }
}

/**
 * Define Breakpoints here.
 */
$mobile-width: 320px;
$tablet-width: 768px;
$desktop-width: 1024px;

@mixin tablet {
    @media (min-width: #{$tablet-width}) and (max-width: #{$desktop-width - 1px}) {
        @content;
    }
}

@mixin desktop {
    @media (min-width: #{$desktop-width}) {
        @content;
    }
}

@mixin tablet-down {
    @media (max-width: #{$tablet-width}) {
        @content;
    }
}

@mixin tablet-up {
    @media (min-width: #{$tablet-width}) {
        @content;
    }
}

@mixin mobile-up {
    @media (min-width: #{$mobile-width}) {
        @content;
    }
}

@mixin mobile {
    @media (min-width: #{$mobile-width}) and (max-width: #{$tablet-width - 1}) {
        @content;
    }
}

@mixin mobile-landscape {
    @media only screen and (min-width: #{$mobile-width}) and (max-width: #{$desktop-width - 1}) and (orientation: landscape) {
        @content;
    }
}

@mixin touch-device {
    // add css interaction media query to detect touch devices
    // refer to: https://caniuse.com/#feat=css-media-interaction
    @media (pointer: coarse) {
        @content;
    }
}

@mixin mobile-tablet-mix {
    @media (min-width: #{$mobile-width}) and (max-width: #{$desktop-width - 1px}) {
        @content;
    }
}

@include mobile {
    /** @define dc-collapsible; weak */
    .dc-collapsible {
        &:before {
            content: '';
            position: absolute;
            pointer-events: none;
            opacity: 1;
            z-index: -1;
            border-radius: $BORDER_RADIUS;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            transition: opacity 0.25s;
            background: var(--general-section-1);
        }
        &--is-expanded {
            background: transparent;

            &:before {
                opacity: 0.75;
            }
        }
    }
    /** @define barrier; weak */
    .barrier,
    .barrier_1,
    .barrier_2,
    .amount {
        .draggable {
            pointer-events: none;

            & .price {
                padding-left: 0;

                &:after {
                    content: none;
                }
            }
        }
        &__widget {
            display: grid;
            grid-template-columns: 3.5fr 1fr;
            height: 4rem;
            margin: 0 0 0.8rem;
            background: var(--general-main-1);
            border-radius: $BORDER_RADIUS;

            &-title {
                font-size: 1.4rem;
                font-weight: 400;
                text-transform: none;
                color: var(--text-less-prominent);
                display: flex;
                align-items: center;
                justify-content: flex-end;
                padding-right: 0.8rem;
            }
            &:is(.amount__widget) {
                grid-template-columns: 5fr 1.3fr;
            }
            .trade-container {
                &__amount {
                    margin: 0.3rem 0 0;
                    .trade-container__input {
                        font-weight: bold;
                        max-width: 100%;
                        padding: 0 4rem;
                    }
                }
            }
        }
        &__fields {
            width: 100%;

            &-input {
                width: 100%;
                padding: 0;
                font-weight: bold;
                border: none;
                display: flex;
                justify-content: center;
                align-items: center;

                &:focus,
                &:active,
                &:hover {
                    border: none;
                }
            }
            &-single {
                margin: 0;
                height: 100%;
                display: flex;
                align-items: center;
            }
            & .dc-tooltip {
                width: 100%;
            }
            & .dc-input-wrapper {
                &__input {
                    outline: 0;
                    border: none;
                }
                &__button {
                    transform: scale(1.4);
                    stroke: var(--text-general);

                    &:hover,
                    &:active {
                        background: none;
                    }
                }
            }
        }
    }
    .barrier__modal {
        &-container {
            display: flex;
            justify-content: space-between;
            .strike--value-container {
                margin-top: 0.5rem;
                margin-right: 2.4rem;
            }
            .trade-container__price-info-currency {
                font-weight: bold;
                font-size: 1.4rem;
                color: var(--text-general);
            }
        }
        &-text {
            margin: 0.5rem 2.4rem 1.6rem;
        }
        &-price {
            margin: 0rem 2.4rem 1.6rem;
        }
        &-input {
            border: 1px solid var(--general-active);
            border-radius: $BORDER_RADIUS;
            margin-inline: 1.6rem;
            &:has(.input:active, .input:focus, .input:hover) {
                border: 1px solid var(--border-active);
            }
            &:has(.input--error) {
                border: 1px solid var(--status-danger) !important;
                .input--error {
                    border: none !important;
                }
            }
        }
    }
    /** @define allow-equals; weak */
    .allow-equals,
    .take-profit.mobile-widget {
        .dc-checkbox__label {
            font-weight: bold;
            color: var(--text-prominent);
            margin-right: 0.8rem;
        }
    }
    /** @define dc-modal; weak */
    .dc-modal {
        &__container_trade-params {
            border-radius: 2px;
            box-shadow: 0 16px 16px 0 var(--shadow-menu-2), 0 0 16px 0 var(--shadow-menu-2);
            overflow-y: scroll;

            /* postcss-bem-linter: ignore */
            .dc-relative-datepicker {
                margin-top: -0.8rem;
                max-width: 110px;
                margin-left: auto;
                margin-right: auto;

                /* iPhone SE screen height fixes due to UI space restrictions */
                @media only screen and (max-height: 480px) {
                    margin-top: -4.6rem;
                }
            }
            /* iPhone SE screen height fixes due to UI space restrictions */
            @media only screen and (max-height: 480px) {
                top: 0.4rem;
            }
        }
        &__container_barrier {
            border-radius: $BORDER_RADIUS * 2;
            box-shadow: 0 16px 16px 0 var(--shadow-menu-2), 0 0 16px 0 var(--shadow-menu-2);
            overflow-y: scroll;
        }
        &-header {
            /* postcss-bem-linter: ignore */
            &--trade-params {
                line-height: 1.4;
                border-bottom-width: 1px;

                /* postcss-bem-linter: ignore */
                .dc-modal-header__close {
                    padding: 0.8rem 0.8rem 0;
                    margin: 0.4rem 0.4rem 0.2rem;
                }
            }
        }
    }
    /** @define dc-tabs; weak */
    .dc-tabs {
        /* postcss-bem-linter: ignore */
        &--trade-params__multiplier-tabs {
            /* postcss-bem-linter: ignore */
            .dc-tabs__content {
                display: flex;
                flex-direction: column;
                min-height: 400px;

                @media only screen and (max-height: 480px) {
                    min-height: 360px;
                }
            }
        }
    }
    /** @define trade-params; weak */
    .trade-params {
        &__error-popup {
            top: 12rem !important;
            opacity: 0.8;
            z-index: 2 !important;

            &--has-numpad {
                z-index: 9999 !important;
                top: 0.8rem !important;
            }
        }
        &__duration {
            &-tickpicker {
                height: 328px;

                .dc-tick-picker {
                    max-width: 100%;
                    height: 100%;
                    align-items: center;
                    justify-content: center;
                }
            }
        }
        &__strike {
            display: flex;
            flex-direction: column;
            .trade-container__stake-field {
                justify-content: space-around;
                margin-top: 1.6rem;
            }
            .strike__pos {
                margin-top: unset;
            }
        }
        &__amount {
            &-keypad {
                width: 100%;
                padding: 1.6rem;
                height: auto;
                margin-top: 0.8rem;
                margin-bottom: 0.8rem;
                display: flex;
                align-items: center;
                justify-content: center;

                &--strike {
                    padding: 0 1.6rem;
                    margin-bottom: 2.4rem;
                }

                .dc-numpad--is-currency,
                .dc-numpad--is-regular {
                    max-width: 100%;
                    grid-template-columns: repeat(4, 1fr);
                    grid-gap: 16px;
                }
                .dc-numpad__increment,
                .dc-numpad__decrement {
                    height: 48px !important;

                    .dc-btn__text {
                        font-size: 3rem;
                        font-weight: normal;
                    }
                    &[disabled] {
                        .dc-btn__text {
                            color: var(--text-disabled);
                        }
                    }
                }
                .dc-numpad__number {
                    border-radius: 2.4rem;
                    background-color: var(--general-section-2);
                    width: 48px;
                    height: 48px !important;
                    font-weight: 700;
                    text-transform: none;
                    line-height: 1.75;
                    color: var(--text-prominent);
                    text-align: left !important;

                    &--is-left-aligned {
                        padding: 0 0 0 0.2rem;
                    }
                }
                .dc-numpad__bkspace,
                .dc-numpad__ok {
                    .dc-numpad__number {
                        height: 100% !important;
                    }
                }
                .dc-numpad__bkspace {
                    .dc-numpad__number {
                        &[disabled] {
                            background-color: var(--general-disabled);
                        }
                        .dc-text {
                            font-size: 1.8rem;
                            /* -webkit-touch-callout only is supported on iOS webkit engine, thus it should apply iOS only styles */
                            @supports (-webkit-touch-callout: none) {
                                @media only screen and (min-width: 360px) {
                                    font-size: 3rem;
                                }
                            }
                        }
                    }
                }
                /* iPhone SE screen height fixes due to UI space restrictions */
                @media only screen and (max-height: 480px) {
                    transform: scale(1, 0.92);
                    transform-origin: top;
                    margin-top: -0.4rem;
                }

                .dc-radio-group {
                    flex-direction: column;
                    width: 100%;
                    gap: 10px;
                    align-items: flex-start;

                    .trade-params__amount--mobile {
                        display: flex;
                        height: 4.8rem;
                        align-items: center;
                        padding: 0 0.8rem;
                        box-sizing: border-box;
                        border: 1px solid var(--general-section-2);
                        border-radius: $BORDER_RADIUS;
                        width: 100%;
                        &:hover {
                            box-shadow: none;
                            border-color: var(--border-active);
                        }
                    }
                }
            }
        }
        &__header {
            @include mobile {
                padding: 0.5rem 0;

                &-label {
                    line-height: 2rem;
                }
                &-value {
                    line-height: 1.8rem;
                    font-size: 1.2rem;

                    &--has-error {
                        color: var(--status-danger);
                        font-weight: bold;
                    }
                }
            }
        }
        &__contract-type-container {
            display: flex;

            .contract-type-widget {
                flex: 1;
            }
        }
        &__multiplier,
        &__accumulator {
            &-radio-group {
                flex-direction: column;
                align-items: flex-start;
                padding: 1.6rem;
                margin-top: 0rem;
                flex: 1;

                &--empty {
                    display: none;
                }
                &:is(.trade-params__accumulator-radio-group) {
                    gap: 1.6rem;
                }
                .dc-radio-group__item {
                    min-height: 4.8rem;
                    max-height: 4.8rem;
                    width: 100%;
                    align-items: center;
                    margin-bottom: 0.8rem;
                    padding: 0.8rem;
                    border-radius: $BORDER_RADIUS;
                    border: 1px solid var(--border-normal);
                    font-size: 1.4rem;
                    flex-direction: row;

                    &--selected {
                        border: 1px solid var(--brand-secondary);
                    }
                    &:is(.trade-params__accumulator-radio-group *) {
                        margin-bottom: 0;
                    }
                }
            }
            &-amount-text {
                padding: 1.6rem 4rem 0;
                line-height: 1.4rem;
                text-align: center;
                color: var(--text-general);
            }
            &-risk-management-dialog {
                display: grid;
                grid-template-rows: auto auto auto 1fr;

                &--no-cancel {
                    grid-template-rows: auto auto 1fr;
                }
                &-bottom-separator {
                    border-bottom: 1px solid var(--border-disabled);
                    height: calc(100% - 1.6rem);
                }
                &-apply-button {
                    display: flex;
                    align-items: flex-end;
                    margin: 0 1.6rem;

                    .dc-btn {
                        flex: 1;
                        height: 4rem;
                    }
                }
                .trade-container__fieldset {
                    padding: 1rem 1.6rem;
                    margin-bottom: 0;
                    border-bottom: 1px solid var(--border-disabled);
                    border-radius: 0;

                    .dc-input-field {
                        z-index: 0;
                    }
                    .dc-popover {
                        padding: 0.6rem 1rem;
                    }
                }
                .dc-checkbox__box {
                    margin-left: 0rem;
                }
                .dc-radio-group {
                    padding: 1.6rem 0rem;
                }
            }
            &-ic-info-wrapper {
                display: flex;
                justify-content: flex-start;
                position: absolute;
                top: 0.6rem;
                left: 0.2rem;
                z-index: 2;

                .dc-popover {
                    padding: 0.5rem 1rem;
                }
            }
            &-deal-cancellation-dialog {
                .dc-checkbox {
                    margin-top: 2.6rem;

                    .dc-checkbox__box {
                        margin-left: 0;
                    }
                }
            }
            &-trade-info {
                display: flex;
                flex-direction: column;
                padding-bottom: 1.6rem;
                align-items: center;

                div:nth-child(2) {
                    padding-top: 1.6rem;
                }

                &-tooltip-text {
                    text-align: right;
                    border-bottom: 1px dotted var(--text-general);
                    display: flex;

                    *:first-child {
                        &:before {
                            content: ': ';
                        }
                    }
                }
            }
        }
        &--modal-wrapper {
            left: 2.4rem !important;

            &__content {
                max-width: 32.8rem;
                left: 2.5rem;
                &--vanilla {
                    top: -0.9rem;
                    left: 2.3rem;
                }
            }
        }
        &--mobile {
            &-strike {
                overflow: auto;
                margin-top: unset;
            }
            &__payout-container {
                display: flex;
                flex-direction: column;
            }
        }
    }
}

.dc-modal__container_trade-params {
    .trade-params__multiplier-ic-info-wrapper {
        margin-left: 1.4rem;
    }
    .dc-modal-header__close {
        margin-right: 1rem;
    }
}
